<template>
  <div id="app">   
    <!-- <Header v-if="mostrarHeader"></Header> 
    <router-view @tokenValido="tokenValido"></router-view> -->
    <div class="container my-5">
        <div class="my-5">
            <h3><i class="fas fa-truck"></i> Equipos</h3>
        </div>
        <table-sortable :items="items"
                        :fields="fields"
                        :rutaNuevo="rutaNuevo"
                        :permisoEscritura="permisoEscritura">

        </table-sortable>
    </div>
  </div>
</template>

<script>
import TableSortable from "@/components/TableSortable"
import servicio from '@/services/servicio-equipos'
    export default{   
      data(){
        return{
            rutaNuevo: "/catalogos/equipos/registro/",
            // rutaNuevo:"equipos/registro/0",
            // modalEspecial: {
            //     id: 'modal-especial',
            //     title: '',
            //     content: '',
            //     codigo:'',
            //     disabled:false
            // },
            items: [],
            fields: [
              { key: 'identificador', label: 'Identificador', sortable: true, class: 'text-center' },
              { key: 'descripcion', label: 'Nombre', sortable: true, sortDirection: 'desc' },
              { key: 'modelo', label: 'Modelo', sortable: true, class: 'text-center' },
              { key: 'tipoEquipo', label: 'Tipo de equipo', sortable: true, class: 'text-center' },
              { key: 'estatus', label: 'Estatus', sortable: true, class: 'text-center' },
              { key: 'actions', label: 'Acciones' },     
            ],
            permisoEscritura: true
        }
      },      
      components:{
        TableSortable
      },
      mounted(){
        this.$loading(true);
        // if(this.verificaTokenValido()){          
          servicio.equipos()
          .then(r=> {    
            this.items = r.data;
            this.$loading(false);
            return r.data
          }).catch(error=>{
            console.log(error);
          });          
          // this.verificaPermiso("equipos");
        // }                
      },
      methods:{

      } 
    }
</script>
